/* eslint-disable jsx-a11y/anchor-is-valid */
/* eslint-disable react-hooks/exhaustive-deps */
import {
  SearchOutlined,
  DeleteOutlined,
  EditOutlined,
  SyncOutlined,
  OrderedListOutlined,
} from "@ant-design/icons";
import {
  Button,
  Form,
  Grid,
  Input,
  Popconfirm,
  Table,
} from "@pankod/refine-antd";
import {useNavigation} from "@pankod/refine-core";
import {Col, message, Modal, Row, Tooltip} from "@pankod/refine-antd";
import {gqlDataProvider} from "api";
import {GradeData, SchoolTypeData} from "apps/core/interfaces";
import {useEffect, useState} from "react";
import ReactTimeAgo from "react-time-ago";
import {CreateSchoolOwnershipFormComponent} from "./forms/create_school_ownership_form";
import {EditWardFormComponent} from "./forms/edit_ward_form";
import {SchoolTypeCurriculumsComponent} from "./school_type_curriculums_component";

interface searchFormData {
  key: string;
}

interface Props {
  height?: any;
  canAdd?: boolean;
}

interface formData {
  name: string;
}

export const GradesComponent: React.FC<Props> = (props: Props) => {
  const [grade, setGrade] = useState<GradeData>();
  const [grades, setGrades] = useState<GradeData[]>([]);
  const [limit, setLimit] = useState(25);
  const [searchKey, setSearchKey] = useState("");
  const [start, setStart] = useState(1);
  const [total, setTotal] = useState(0);
  const [hasNext, setHasNext] = useState(false);
  const [loading, setLoading] = useState(true);
  const {push} = useNavigation();
  const [searchForm] = Form.useForm<searchFormData>();
  const [form] = Form.useForm<formData>();
  const breakpoint = Grid.useBreakpoint();
  const isMobile = !breakpoint.lg;

  const [createModal, setCreateModal] = useState(false);
  const [editModal, setEditModal] = useState(false);

  const syncGrades = async () => {
    const {data} = await gqlDataProvider.custom!({
      url: "",
      method: "post",
      metaData: {
        operation: "synchronizeGrades",
        variables: {},
        fields: ["success", "message"],
      },
    });

    if (data.success) {
      message.success(data.message);
      getGrades(1);
    }
  };

  const confirmDelete = async (id: number) => {
    const {data} = await gqlDataProvider.custom!({
      url: "",
      method: "post",
      metaData: {
        operation: "deleteGrade",
        variables: {
          id: {
            value: parseInt(id.toString()),
            type: "Int",
            required: true,
          },
        },
        fields: ["success", "message"],
      },
    });

    if (data.success) {
      message.success(data.message);
      setGrades(grades.filter((item) => item.id !== id));
    } else {
      message.error(data.message);
    }
  };

  const cancelDelete = () => {
    message.info("Canceled");
  };

  const columns = [
    {
      title: "Name",
      dataIndex: "name",
      fixed: true,
      render: (text: any, row: any, index: any) => (
        <span>
          <a>{row.name}</a>
        </span>
      ),
    },
    {
      title: "Created",
      dataIndex: "created",
      render: (text: any, row: any, index: any) => (
        <>
          <span>
            <ReactTimeAgo date={new Date(row.created)} />
          </span>
        </>
      ),
    },
    {
      title: "Status",
      dataIndex: "isActive",
      render: (text: any, row: any, index: any) => (
        <a>
          {row?.isActive ? (
            <span style={{color: "green"}}>Active</span>
          ) : (
            <span style={{color: "red"}}>Inactive</span>
          )}
        </a>
      ),
    },
    {
      title: "",
      dataIndex: "action",
      render: (text: any, row: any, index: any) => (
        <div style={{display: "flex", justifyContent: "flex-end"}}>
          <Popconfirm
            title={"Are you sure to delete this School Ownership?"}
            onConfirm={() => confirmDelete(row?.id)}
            onCancel={() => cancelDelete}
            okText="Yes"
            cancelText="No"
            disabled={!row?.canDelete}
          >
            <Button
              style={{marginRight: 3}}
              icon={<DeleteOutlined />}
              disabled={!row?.canDelete}
              danger
            ></Button>
          </Popconfirm>

          <Tooltip
            title={"Edit " + row?.name + " Ownership"}
            color={"green"}
            placement="left"
          >
            <Button
              style={{marginRight: 3}}
              icon={<EditOutlined />}
              disabled={!row?.canEdit}
              onClick={() => {}}
              danger
            ></Button>
          </Tooltip>
        </div>
      ),
    },
  ];

  const getGrades = async (
    start: number,
    key: string = "",
    limit: number = 25
  ) => {
    const {data} = await gqlDataProvider.custom!({
      url: "",
      method: "get",
      metaData: {
        operation: "grades",
        variables: {
          page: {value: start, type: "Int", required: true},
          key: {value: key, type: "String", required: false},
          pageSize: {value: limit, type: "Int", required: true},
        },
        fields: [
          "total",
          "page",
          "pages",
          "hasNext",
          "hasPrev",
          {
            results: [
              "id",
              "name",
              "code",
              "isActive",
              "created",
              "updated",
              "canEdit",
              "canDelete",
              "canManage",
            ],
          },
        ],
      },
    });
    setStart(data.page);
    setTotal(data.total);
    setHasNext(data.hasNext);
    setGrades([...data.results]);
    setLoading(false);
  };

 

  useEffect(() => {
    getGrades(start);
  }, []);

  return (
    <>
      <Row style={{marginTop: 10}}>
        <Col span={isMobile ? 24 : 20}>
          <Form<searchFormData>
            layout="vertical"
            form={searchForm}
            onFinish={(values) => {
              setStart(1);
              grades.length = 0;
              getGrades(1, values.key);
            }}
          >
            <Form.Item name="key">
              <Input
                size="large"
                placeholder="Search ..."
                prefix={<SearchOutlined />}
                autoComplete="off"
                allowClear
              />
            </Form.Item>
          </Form>
        </Col>

        <Col
          span={isMobile ? 24 : 4}
          style={{display: "flex", justifyContent: "flex-end"}}
        >
          <Tooltip title={"Synchronize Default Grades"}>
            <Button
              size="large"
              icon={<SyncOutlined />}
              onClick={() => syncGrades()}
              danger
            ></Button>
          </Tooltip>
        </Col>
      </Row>

      <Table
        loading={loading}
        columns={columns}
        dataSource={grades}
        scroll={{x: true}}
        pagination={{
          onChange: (page, pageSize) => {
            setLimit(pageSize);
            getGrades(page, searchKey, pageSize);
          },
          total: total,
          pageSize: limit,
          position: ["bottomCenter"],
          showQuickJumper: false,
        }}
      />

      <Modal
        title="Edit Grade"
        width={"60vw"}
        visible={editModal}
        onOk={()=>{}}
        onCancel={()=>{}}
        footer={[]}
      >
      </Modal>

      <Modal
        title="Create Grade"
        width={"60vw"}
        visible={createModal}
        onOk={()=>{}}
        onCancel={()=>{}}
        footer={[]}
      >
      </Modal>
    </>
  );
};
